  border-radius: 20px;
  margin-right: 10px;
  padding: 10px;
  box-shadow: 0 4px 33px rgba(0, 0, 0, .07);
}

.hero-accordeon-list {
  flex-direction: column;
  gap: 9px;
  display: flex;
  position: relative;
}

.hero-accordeon-item {
  width: 85px;
  transition: var(--main-trans);
  border-radius: 26.5px 0 0 26.5px;
  padding: 4px 0 4px 5px;
}

.accordeon {
  visibility: hidden;
  opacity: 0;
  z-index: 2;
  max-width: 258px;
  height: 100%;
  background: var(--yellow);
  transition: all var(--main-trans);
  border-radius: 20px;
  padding: 19px 16px 25px;
  position: absolute;
  top: 0;
  right: 0;
}

.hero-accordeon-item:hover, .hero-accordeon-item:focus {
  background-color: var(--yellow);
  transition: var(--main-trans);
}

.hero-accordeon-item:hover .accordeon, .hero-accordeon-item:focus .accordeon {
  visibility: visible;
  opacity: 1;
  transition: all var(--main-trans);
}

.hero-accordeon-item:hover .accordeon-title, .hero-accordeon-item:focus .accordeon-title {
  color: var(--main-dark-grey);
  transition: var(--main-trans);
}

.accordeon-title {
  cursor: pointer;
  color: var(--light-grey);
  text-align: justify;
  transition: var(--main-trans);
